<style scoped>
  
  .clear {
    clear: both;
  }
  
  .line-vertical {
    margin: 0 auto;
    width: 1px;
    border-left: solid silver 1px;
  }

  .line-double {
    margin: 0 auto;
    width: 240px;
    border-top: solid silver 1px;
    border-left: solid silver 1px;
    border-right: solid silver 1px;
  }
  
</style>

<template>
  <main-layout>

    <ui-container fluid>
      <ui-panel title="Registration Simulation" :loading="loading" icon="users">

        <ui-row v-if="member.up.id" form-group>
          <ui-col size="md-12">
      
            <ui-figure @click="registerMember" :data-source="member.up" />

          </ui-col>
          <ui-col size="md-12">
            <div class="line-vertical">&nbsp;</div>
          </ui-col>
        
          <ui-col size="md-12">
            <div class="line-double">&nbsp;</div>
          </ui-col>
          <ui-col size="md-12">
            <ui-figure @click="registerMember" :data-source="member.left" hr-align="left" />
            <ui-figure @click="registerMember" :data-source="member.right" hr-align="right" />
          </ui-col>
        </ui-row>
        <!-- <ui-row v-else>
          <ui-col size="md-12">

          </ui-col>
        </ui-row> -->

        <div class="clear"></div>

      </ui-panel>
    </ui-container>

    <ui-buttonbar>
      <ui-button v-if="memberPosActive" 
      bs-type="danger" 
      @click="memberPosActive = ''" 
      icon="angle-double-up"></ui-button>

      <ui-button bs-type="success" @click="refreshData" :loading="loading" icon="refresh" />
      <ui-button href="#/registration-simulation/add-new-data" icon="plus"></ui-button>
    </ui-buttonbar>

    <ui-modal @close="deleteDialog = false" :display="deleteDialog" title="Confirm" icon="question">
      <div slot="message">Delete data "{{ rowActive.title }}"?</div>
      <div slot="buttonbar">
        <ui-button @click="removeData">OK</ui-button>
        <ui-button @click="deleteDialog = false" bs-type="danger">Cancel</ui-button>
      </div>
    </ui-modal>

  </main-layout>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex';
  import genealogy from '../../genealogy';

  export default {

    data() {
      return {
        loading: false,
        deleteDialog: false,

        memberPosActive: '',
      };
    },

    computed: {

      ...mapGetters('MemberNetworkStore', {
        rowActive: 'rowActive',
      }),

      member() {
        return genealogy.getMemberByPos(this.memberPosActive, this.rowActive);
      },

    },

    mounted() {
      this.refreshData();
    },

    methods: {

      ...mapActions('MemberNetworkStore', {
        fetchData: 'fetchData',
        deleteData: 'deleteData',
        moveData: 'moveData',
      }),

      getAvatar(member) {
        return localStorage.archivesUrl + '/images/member/' + member.id + '/small_' + member.avatar;
      },

      removeData() {
        this.deleteDialog = false;
        this.deleteData();
      },

      refreshData() {
        this.loading = true;
        this.fetchData().then(result => {
          this.loading = false;
        });
      },

      registerMember(data) {
        if (data.available) {
          this.memberPosActive = data.member.pos;
        }
        else {
          this.$router.push('/registration-simulation/' + this.member.up.id);
        }
      },

    },

  }
</script>



