<template>
  <main-layout>
    
    <ui-container fluid>

      <ui-panel title="Landing Page" icon="eye" :loading="loading">

        <ui-row form-group>
          <ui-col size="md-1">Title</ui-col>
          <ui-col size="md-11">
            <ui-label v-model="form.title" />
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">Content</ui-col>
          <ui-col size="md-11">
            <ui-label v-model="form.content" />
          </ui-col>
        </ui-row>
        
        <ui-row form-group>
          <ui-col size="md-1">Status</ui-col>
          <ui-col size="md-11">
            <ui-label v-model="form.statusname" />
          </ui-col>
        </ui-row>

      </ui-panel>
    </ui-container>

    <ui-buttonbar>
      <ui-button bs-type="success" 
      @click="refreshData" 
      :loading="loading" 
      icon="refresh" />

      <ui-button icon="edit"
      href="#/landing-page/edit-data" />
    </ui-buttonbar>

  </main-layout>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex';

  export default {

    data() {
      return {
        loading: false,
      };
    },

    computed: {
      ...mapGetters('LandingPageStore', {
        form: 'rowActive',
      }),
    },

    mounted() {
      this.refreshData();
    },

    methods: {
      
      ...mapActions('LandingPageStore', {
        fetchData: 'fetchData',
      }),

      async refreshData() {
        this.loading = true;
        await this.fetchData();
        this.loading = false;
      },

    },

  }
</script>



