<template>
  <main-layout id="GenealogySponsorEditDataView">
    <ui-splash :display="splash">{{ result.message }}</ui-splash>

    <ui-container fluid>

      <ui-alert :data-source="result.data"
      :display="result.state == 'invalid'"
      @close="result.state = ''">
        {{ result.message }}
      </ui-alert>

      <ui-panel title="Genealogy Sponsor" icon="eye">

        <ui-row form-group>
          <ui-col size="md-2">Username</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="rowActive.member.username"></ui-label>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-2">Fullname</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="rowActive.member.fullname"></ui-label>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-2">Sponsored</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="rowActive.sponsor.fullname"></ui-label>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-2">Status</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="rowActive.member.statusname"></ui-label>
          </ui-col>
        </ui-row>


      </ui-panel>
    </ui-container>

    <ui-buttonbar>
      <ui-button @click="$router.go(-1)" 
        bs-type="success"
        icon="arrow-left" />


    </ui-buttonbar>


  </main-layout>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex';

  export default {

    /************************
    <id="_data">
    ************************/
    data() {
      return {
        loading: false,
        splash: false,
        deleteDialog: false,

        result: {
          state: '',
          message: '',
          data: [],
        },

        
      };
    },

    computed: {
      ...mapGetters('GenealogySponsorStore', {
        rowActive: 'rowActive',
      }),
    },

    /************************
    <id="_mounted">
    ************************/
    mounted() {
      this.moveData(this.$route.params.id);

    },

    methods: {
      ...mapActions('GenealogySponsorStore', {
        moveData: 'moveData',
      }),


    },

  }
</script>



