<template>
  <main-layout>
    
    <ui-container fluid>

      <ui-panel title="Registration Simulation" icon="users">
        <h5>Please select the package member;</h5>
        <hr>
        <ui-row>

          <ui-col v-for="(memberPackage, i) in rsMemberPackage" 
          :key="i"
          size="xs-6">
            <ui-box icon="cube" 
            @click="moveData(memberPackage.id)"
            :title="memberPackage.name" 
            :value="parseInt(memberPackage.price).formatMoney()" 
            :href="getRegisterUrl(memberPackage.id)" />
          </ui-col>

        </ui-row>        
        <hr>

      </ui-panel>
    </ui-container>

    <ui-buttonbar>
      <ui-button @click="$router.go(-1)" 
      bs-type="danger"
      icon="arrow-left" />

      <ui-button bs-type="success" 
      @click="refreshData" 
      :loading="loading" 
      icon="refresh" />
    </ui-buttonbar>

    <!-- <ui-modal @close="deleteDialog = false" :display="deleteDialog" title="Confirm" icon="question">
      <div slot="message">Delete data "{{ rowActive.title }}"?</div>
      <div slot="buttonbar">
        <ui-button @click="removeData">OK</ui-button>
        <ui-button @click="deleteDialog = false" bs-type="danger">Cancel</ui-button>
      </div>
    </ui-modal> -->

  </main-layout>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex';

  export default {

    data() {
      return {
        loading: false,
        upline: '',
      };
    },

    mounted() {
      this.reloadParams();
      this.refreshData();
    },

    watch: {
      $router() {
        this.reloadParams();
      },
    },

    computed: {

      ...mapGetters('MemberPackageStore', {
        rsMemberPackage: 'rs',
      }),

    },

    methods: {

      ...mapActions('MemberPackageStore', {
        fetchData: 'fetchData',
        moveData: 'moveData',
      }),

      getRegisterUrl(id) {
        return '#/registration-simulation/' + this.upline + '/' + id;
      },

      reloadParams() {
        this.upline = this.$route.params.upline;
      },

      refreshData() {
        this.loading = true;
        this.fetchData().then(result => {
          this.loading = false;
        });
      },

    },

  }
</script>



