<template>
  <main-layout>
    
    <ui-container fluid>

      <ui-alert :data-source="result.data"
      :display="result.state == 'invalid'"
      @close="result.state = ''">
        {{ result.message }}
      </ui-alert>

      <ui-panel title="Transfer Ewallet to ECash" icon="eye" :loading="loading">

        <ui-row form-group>
          <ui-col size="md-3">Current EWallet balance:</ui-col>
          <ui-col size="md-9">
            
            <b>Rp. {{rowExtra.ebalance}}</b>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-3">Enter the amount to be transferred:</ui-col>
          <ui-col size="md-9">
            <ui-textbox  v-model="form.amount" ></ui-textbox>
          </ui-col>
        </ui-row>
        
        
      </ui-panel>
    </ui-container>

    <ui-buttonbar>
      <ui-button bs-type="success" 
      @click="refreshData" 
      :loading="loading" 
      icon="refresh" />

      <ui-button @click="saveData" 
      :loading="loading" 
      icon="floppy-o" />
    </ui-buttonbar>

  </main-layout>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex';

  export default {

    data() {
      return {
        loading: false,

        result: {
          state: '',
          message: '',
          data: [],
        },

        form: {
          amount: '',
        },

      };
    },

    computed: {
      ...mapGetters('TransferToEcashStore', {
        rowActive: 'rowActive',
        rowExtra: 'rowExtra',
      }),
    },

    mounted() {
      this.refreshData();
    },

    methods: {
      
      ...mapActions('TransferToEcashStore', {
        fetchData: 'fetchData',
      }),

      async refreshData() {
        this.loading = true;
        await this.fetchData();
        this.loading = false;
      },

      saveData() {
        this.loading = true;

        api.post('transferToEcash/storeData', this.form).then(result => {

          if (result.state == 'success') {
            this.result = result;
            this.splash = true;

            this.$router.push('/saldo-transaksi-ewallet/all-data');
          } else
            this.loading = false;
            this.result = result;
        });

      },

    },

  }
</script>



