<template>
  <main-layout id="SaldoTransaksiEwalletEditDataView">
    <ui-splash :display="splash">{{ result.message }}</ui-splash>

    <ui-container fluid>

      <ui-alert :data-source="result.data"
      :display="result.state == 'invalid'"
      @close="result.state = ''">
        {{ result.message }}
      </ui-alert>

      <ui-panel title="Saldo Transaksi Ewallet" icon="eye">

        <ui-row form-group>
          <ui-col size="md-2">Type</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.type"></ui-label>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-2">Credit</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.credit"></ui-label>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-2">Debit</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.debit"></ui-label>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-2">Note</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.note"></ui-label>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-2">Date</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.created_on"></ui-label>
          </ui-col>
        </ui-row>

      </ui-panel>
    </ui-container>

    <ui-buttonbar>
      <ui-button @click="$router.go(-1)" 
        bs-type="success"
        icon="arrow-left" />
    </ui-buttonbar>


  </main-layout>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex';

  export default {

    data() {
      return {
        loading: false,
        splash: false,
        deleteDialog: false,

        result: {
          state: '',
          message: '',
          data: [],
        },

        form: {
          id: '',
          type: '',
          credit: '',
          debit: '',
          note: '',
          created_on: '',
        },
      };
    },

    computed: {
      ...mapGetters('SaldoTransaksiEwalletStore', {
        rowActive: 'rowActive',
      }),
    },

    mounted() {
      this.moveData(this.$route.params.id);

      this.form.id = this.rowActive.id;
      this.form.type = this.rowActive.type;
      this.form.credit = this.rowActive.credit;
      this.form.debit = this.rowActive.debit;
      this.form.note = this.rowActive.note;
      this.form.created_on = this.rowActive.created_on;

    },

    methods: {

      ...mapActions('SaldoTransaksiEwalletStore', {
        moveData: 'moveData',
      }),


    },

  }
</script>



