<template>
  <main-layout id="BonusRewardDetailDataView">
    <ui-splash :display="splash">{{ result.message }}</ui-splash>

    <ui-container fluid>

      <ui-alert :data-source="result.data"
      :display="result.state == 'invalid'"
      @close="result.state = ''">
        {{ result.message }}
      </ui-alert>

      <ui-panel title="Bonus Reward Jalan jalan" icon="eye">

        <ui-row form-group>
          <ui-col size="md-2">Name</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.name"></ui-label>
          </ui-col>
        </ui-row>

        

        <ui-row form-group>
          <ui-col size="md-2">Start Period</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.start_period"></ui-label>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-2">End Period</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.end_period"></ui-label>
          </ui-col>
        </ui-row>
        
        <ui-row form-group>
          <ui-col size="md-2">Syarat PR Kanan dan Kiri</ui-col>
          <ui-col size="md-5">
            <ui-label v-model="form.pv"></ui-label>
          </ui-col>
          <ui-col size="md-5">
            <ui-label v-model="form.pv"></ui-label>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-2">Bonus</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.bonus"></ui-label>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-2">Balance PR Anda</ui-col>
          <ui-col size="md-5">
            <ui-label v-model="form.pv_right"></ui-label> 
          </ui-col>
          <ui-col size="md-5">
            <ui-label v-model="form.pv_left"></ui-label>
          </ui-col>
        </ui-row>


      </ui-panel>
    </ui-container>

    <ui-buttonbar>
      <ui-button @click="kembali" 
      bs-type="success"
      icon="arrow-left" />

    <ui-button  
    v-if="dataPv.length > 0 &&  parseInt(dataPv[0].status)!==2 && rowActive.id==dataPv[0].reward_id && parseInt(dataPv[0].pv_left) >= parseInt(rowActive.pv) && parseInt(dataPv[0].pv_right) >= parseInt(rowActive.pv) " 
    @click="saveData" 
    bs-type="danger"
      :loading="loading" ><span style="font-size: 11px">Klaim</span></ui-button>
    </ui-buttonbar>
    

  </main-layout>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex';

  export default {

    data() {
      return {
        loading: false,
        splash: false,
        deleteDialog: false,

        result: {
          state: '',
          message: '',
          data: [],
        },

        form: {
          id: '',
          name: '',
          bonus: '',
          pv: '',
          start_period: '',
          end_period: '',
          status: '',
        },
      };
    },

    computed: {
      ...mapGetters('BonusRewardStore', {
        rowActive: 'rowActive',
        dataPv: 'dataPv',
      }),
    },

    mounted() {
      
      this.moveData(this.$route.params.id);

      this.form.id = this.rowActive.id;
      this.form.name = this.rowActive.name;
      this.form.bonus = this.rowActive.bonus;
      this.form.pv = this.rowActive.pv;
      this.form.start_period = this.rowActive.start_period;
      this.form.end_period = this.rowActive.end_period;

      this.moveDataPv();
      if (this.dataPv.length > 0){
            this.form.pv_left = this.dataPv[0].pv_left;
            this.form.pv_right = this.dataPv[0].pv_right;
      }else{
            this.form.pv_left = '0';
            this.form.pv_right = '0';
          }


    },

    methods: {

      ...mapActions('BonusRewardStore', {
        fetchData: 'fetchData',
        moveData: 'moveData',
        readOneData: 'readOneData',
        storeData: 'storeData',
      }),


      moveDataPv() {
        this.loading = true;
        this.readOneData(this.form).then(result => {
        this.loading = false;  
        });
      },


      kembali() {
        
        this.loading = true;
        this.fetchData().then(result => {
          this.$router.push('/bonus-reward/all-data');
          this.loading = false;
        });
        
      },

      saveData() {
        this.loading = true;

        this.storeData(this.form).then(result => {

          this.result = result;
          this.splash = true;

          setTimeout(() => {
            this.splash = false;
            this.loading = false;

            this.fetchData().then(result => {
              this.$router.push('/bonus-reward/all-data');
              this.loading = false;
            });

          }, 2000);

        }).catch(result => {

          this.loading = false;
          this.result = result;

        });
      },


    },

  }
</script>



