<template>
  <main-layout>
    <ui-splash :display="splash">{{ result.message }}</ui-splash>

    <ui-container fluid>

      <ui-alert :data-source="result.data"
      :display="result.state == 'invalid'"
      @close="result.state = ''">
        {{ result.message }}
      </ui-alert>

      <ui-panel title="Landing Page" icon="edit" :loading="loading">

        <ui-row form-group>
          <ui-col size="md-1">Title</ui-col>
          <ui-col size="md-11">
            <ui-textbox v-model="form.title" />
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">Content</ui-col>
          <ui-col size="md-11">
            <ui-textarea v-model="form.content" />
          </ui-col>
        </ui-row>
        
        <ui-row form-group>
          <ui-col size="md-1">Status</ui-col>
          <ui-col size="md-11">
            <ui-select :data-source="rsPublicationStatus"
            v-model="form.status"
            data-field="status" />            
          </ui-col>
        </ui-row>

      </ui-panel>
    </ui-container>

    <ui-buttonbar>
      <ui-button @click="$router.go(-1)" 
      bs-type="success"
      icon="arrow-left" 
      :disabled="loading" />

      <ui-button @click="saveData" 
      :loading="loading" 
      icon="floppy-o" />
    </ui-buttonbar>

  </main-layout>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex';

  export default {

    data() {
      return {
        loading: false,
        splash: false,
    
        result: {
          state: '',
          message: '',
          data: [],
        },

        form: {},
      };
    },

    computed: {
      ...mapGetters('PublicationStatusStore', {
        rsPublicationStatus: 'rs',
      }),
      ...mapGetters('LandingPageStore', {
        rowActive: 'rowActive',
      }),
    },

    async mounted() {
      this.loading = true;

      await this.fetchData();
      this.form = this.rowActive;
      
      this.loading = false;
    },

    methods: {

      ...mapActions('LandingPageStore', {
        fetchData: 'fetchData',
        storeData: 'storeData',
      }),

      saveData() {
        this.loading = true;

        this.storeData(this.form).then(result => {

          this.result = result;
          this.splash = true;

          setTimeout(() => {
            this.splash = false;
            this.loading = false;

            this.$router.go(-1);
          }, 2000);

        }).catch(result => {

          this.loading = false;
          this.result = result;

        });
      },

    },

  }
</script>



