<style scoped>
  .ui-col {
    padding: 16px;
    padding-bottom: 8px;
  }

  .box {
    width: 100%;
    height: 100px;
    border: solid black 1px;
    background: #449d44;
    box-shadow: silver 2px 2px 2px;
    text-align: center;
  }

  .box label {
    color: rgba(0, 0, 0, 0.7);
    display: block;
    font-size: 14px;
  }

  .box b {
    color: white;
    font-size: 20px;
    text-shadow: black 2px 2px 2px;
  }
</style>
<template>
  <main-layout id="SaldoTransaksiEwalletAllDataView">

    <ui-container fluid>
      <ui-panel title="Saldo Transaksi Ewallet" :loading="loading" icon="list">

        <ui-row form-group>
          <ui-col size="md-4">
          <div class="box">
              <label>Credit</label>
              <b>Rp.<br>{{rowExtra.ewallet_credit}}</b>
            </div>
          </ui-col>
          <ui-col size="md-4">
          <div class="box">
              <label>Debit</label>
              <b>Rp.<br>{{rowExtra.ewallet_debit}}</b>
            </div>
          </ui-col>
          <ui-col size="md-4">
          <div class="box">
              <label>Balance</label>
              <b>Rp.<br>{{rowExtra.ebalance}}</b>
            </div>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-12">

            <ui-list @tap="openData"
            :data-source="rs"
            data-field="bv"
            data-extra="date" />

          </ui-col>
        </ui-row>

      </ui-panel>
    </ui-container>

    <ui-buttonbar>
      <ui-button bs-type="success" @click="refreshData" :loading="loading" icon="refresh"></ui-button>
    </ui-buttonbar>

  </main-layout>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex';

  export default {

    data() {
      return {
        loading: false,
        deleteDialog: false,
      };
    },

    computed: {

      ...mapGetters('SaldoTransaksiEwalletStore', {
        rs: 'rs',
        rowActive: 'rowActive',
        rowExtra: 'rowExtra',
      }),

    },

    mounted() {
      if (this.rs.length <= 0)
        this.refreshData();
    },

    methods: {

      ...mapActions('SaldoTransaksiEwalletStore', {
        fetchData: 'fetchData',
      }),

      refreshData() {
        this.loading = true;
        this.fetchData().then(result => {
          this.loading = false;
        });
      },

      openData(value) {
        this.$router.push('/saldo-transaksi-ewallet/edit-data/' + value);
      },

    },

  }
</script>



