<template>
  <main-layout id="PendingMembersDetailDataView">
    <ui-splash :display="splash">{{ result.message }}</ui-splash>

    <ui-container fluid>

      <ui-alert :data-source="result.data"
      :display="result.state == 'invalid'"
      @close="result.state = ''">
        {{ result.message }}
      </ui-alert>

      <ui-panel title="Pending Members" icon="eye">

        <ui-row form-group>
          <ui-col size="md-2">Avatar</ui-col>
          <ui-col size="md-10">
            <ui-image v-model="form.avatar" />
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-2">Username</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.username"></ui-label>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-2">Nama Lengkap</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.fullname"></ui-label>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-2">Tipe Pendaftaran</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.type"></ui-label>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-2">Tanggal Lahir</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.birth_day"></ui-label>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-2">Email</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.email"></ui-label>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-2">No. Telepon</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.phone"></ui-label>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-2">Terdaftar Pada</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.benificiery_relation"></ui-label>
          </ui-col>
        </ui-row>


      </ui-panel>
    </ui-container>

    <ui-buttonbar>
      <ui-button @click="$router.go(-1)" 
        bs-type="success"
        icon="arrow-left" />


    </ui-buttonbar>


  </main-layout>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex';

  export default {

    data() {
      return {
        loading: false,
        splash: false,
        deleteDialog: false,

        result: {
          state: '',
          message: '',
          data: [],
        },

        form: {
          id: '',
          avatar: '',
          username: '',
          fullname: '',
          type: '',
          birth_day: '',
          email: '',
          phone: '',
          benificiery_relation: '',
        },
      };
    },

    computed: {

      ...mapGetters('PendingMembersStore', {
        rowActive: 'rowActive',
      }),
    },


    mounted() {
      this.moveData(this.$route.params.id);

      this.form.id = this.rowActive.id;
      this.form.avatar = this.rowActive.avatar;
      this.form.username = this.rowActive.username;
      this.form.fullname = this.rowActive.fullname;
      this.form.type = this.rowActive.type;
      this.form.birth_day = this.rowActive.birth_day;
      this.form.email = this.rowActive.email;
      this.form.phone = this.rowActive.phone;
      this.form.benificiery_relation = this.rowActive.benificiery_relation;

    },

    methods: {

      ...mapActions('PendingMembersStore', {
        moveData: 'moveData',
      }),


    },

  }
</script>



