<template>
  <main-layout id="RiwayatBonusDanPajakAllDataView">

    <ui-container fluid>
      <ui-panel title="Riwayat Bonus dan Pajak" :loading="loading" icon="list">

        <ui-row form-group>
          <ui-col size="md-1">Month</ui-col>
          <ui-col size="md-5">
            <ui-select :data-source="rsMonth"
            v-model="form.month"
            @input="refreshData"
            data-field="month" />
          </ui-col>

          <ui-col size="md-1">Year</ui-col>
          <ui-col size="md-5">
            <ui-select :data-source="rsYear"
            v-model="form.year"
            @input="refreshData"
            data-value="year"
            data-field="year" />
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-12">

            <ui-list @tap="openData"
            :data-source="rs"
            data-extra="date"
            data-field="bonus" />

          </ui-col>
        </ui-row>

      </ui-panel>
    </ui-container>

    <ui-buttonbar>
      <ui-button bs-type="success" @click="refreshData" :loading="loading" icon="refresh"></ui-button>
    </ui-buttonbar>

  </main-layout>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex';

  export default {

    /************************
    <id="_data">
    ************************/
    data() {
      let d = new Date();
      let year = d.getFullYear();
      let month = d.getMonth() + 1;

      return {
        loading: false,
        deleteDialog: false,

        form: {
          year,
          month,
        },

      };
    },

    computed: {

      /************************
      <id="_mapGetters">
      ************************/
      ...mapGetters('RiwayatBonusDanPajakStore', {
        rs: 'rs',
        rsYear: 'rsYear',
        rowActive: 'rowActive',
      }),
      ...mapGetters('MonthStore', {
        rsMonth: 'rs',
      }),

    },

    /************************
    <id="_mounted">
    ************************/
    mounted() {
      this.refreshData();
    },

    /************************
    <id="_methods">
    ************************/
    methods: {

      /************************
      <id="_mapActions">
      ************************/
      ...mapActions('RiwayatBonusDanPajakStore', {
        fetchData: 'fetchData',
        fetchYear: 'fetchYear',
        moveData: 'moveData',
      }),

      /************************
      <id="_refreshData">
      ************************/
      async refreshData() {
        this.loading = true;

        await this.fetchYear();
        await this.fetchData(this.form);

        this.loading = false;
      },

      /************************
      <id="_openData">
      ************************/
      openData(value) {
        this.$router.push('/riwayat-bonus-dan-pajak/detail-data/' + value);
      },

    },

  }
</script>



