<template>
  <main-layout id="PendingMembersAllDataView">

    <ui-container fluid>
      <ui-panel title="Pending Members" :loading="loading" icon="list">

        <ui-row form-group>
          <ui-col size="md-12">Cari</ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-12">
          <ui-textbox v-model="form.cari" placeholder="Cari..."  />
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-12">

            <ui-list @tap="openData"
            :data-source="rsMember"
            data-field="bv" />

          </ui-col>
        </ui-row>

      </ui-panel>
    </ui-container>

    <ui-buttonbar>
      <ui-button bs-type="success" @click="refreshData" :loading="loading" icon="refresh"></ui-button>
    </ui-buttonbar>

  </main-layout>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex';

  export default {

    data() {
      return {
        loading: false,
        deleteDialog: false,
        
        form: {
          cari: '',
        },

      };
    },

    computed: {

      ...mapGetters('PendingMembersStore', {
        rs: 'rs',
        rowActive: 'rowActive',
      }),

      rsMember(){
        return this.rs.filter(member=>{
          return member.first_name.toLowerCase().indexOf(this.form.cari.toLowerCase())> -1;
        })

      },

    },

    mounted() {
        this.refreshData();
    },

    methods: {
      ...mapActions('PendingMembersStore', {
        fetchData: 'fetchData',
        moveData: 'moveData',
      }),

      refreshData() {
        this.loading = true;
        this.fetchData().then(result => {
          this.loading = false;
        });
      },

      openData(value) {
        this.$router.push('/pending-members/detail-data/' + value);
      },

    },

  }
</script>



