<template>
  <main-layout>
    
    <ui-container fluid>

      <ui-panel title="Update Phone Number" icon="eye" :loading="loading">

        <ui-row form-group>
          <ui-col size="md-1">Phone</ui-col>
          <ui-col size="md-11">
            <ui-label v-model="form.phone" />
          </ui-col>
        </ui-row>

      </ui-panel>
    </ui-container>

    <ui-buttonbar>
      <ui-button bs-type="success" 
      @click="refreshData" 
      :loading="loading" 
      icon="refresh" />

      <ui-button icon="edit"
      href="#/update-phone-number/edit-data" />
    </ui-buttonbar>

  </main-layout>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex';

  export default {

    data() {
      return {
        loading: false,
      };
    },

    computed: {
      ...mapGetters('UpdatePhoneNumberStore', {
        form: 'rowActive',
      }),
    },

    mounted() {
      this.refreshData();
    },

    methods: {
      
      ...mapActions('UpdatePhoneNumberStore', {
        fetchData: 'fetchData',
      }),

      async refreshData() {
        this.loading = true;
        await this.fetchData();
        this.loading = false;
      },

    },

  }
</script>



