<template>
  <main-layout id="GenealogySponsorEditDataView">
    <ui-splash :display="splash">{{ result.message }}</ui-splash>

    <ui-container fluid>

      <ui-alert :data-source="result.data"
      :display="result.state == 'invalid'"
      @close="result.state = ''">
        {{ result.message }}
      </ui-alert>

      <ui-panel title="Genealogy Sponsor" icon="eye">

        <ui-row form-group>
          <ui-col size="md-2">Sponsor</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.sponsor_id"></ui-label>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-2">Level</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.level"></ui-label>
          </ui-col>
        </ui-row>



      </ui-panel>
    </ui-container>

    <ui-buttonbar>
      <ui-button @click="$router.go(-1)" 
        bs-type="success"
        icon="arrow-left" />


    </ui-buttonbar>


  </main-layout>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex';

  export default {

    /************************
    <id="_data">
    ************************/
    data() {
      return {
        loading: false,
        splash: false,
        deleteDialog: false,

        result: {
          state: '',
          message: '',
          data: [],
        },

        form: {
          id: '',
          sponsor_id: '',
          level: '',
        },
      };
    },

    /************************
    <id="_computed">
    ************************/
    computed: {
      /************************
      <id="_mapGetters">
      ************************/
      ...mapGetters('GenealogySponsorStore', {
        rowActive: 'rowActive',
      }),
    },

    /************************
    <id="_mounted">
    ************************/
    mounted() {
      this.moveData(this.$route.params.id);

      this.form.id = this.rowActive.id;
      this.form.sponsor_id = this.rowActive.sponsor_id;
      this.form.level = this.rowActive.level;

    },

    /************************
    <id="_methods">
    ************************/
    methods: {

      /************************
      <id="_mapActions">
      ************************/
      ...mapActions('GenealogySponsorStore', {
        moveData: 'moveData',
        updateData: 'updateData',
      }),


    },

  }
</script>



