<template>
  <main-layout>
    
    <ui-container fluid>

      <ui-alert :data-source="result.data"
      :display="result.state == 'invalid'"
      @close="result.state = ''">
        {{ result.message }}
      </ui-alert>

      <ui-panel title="Transfer Ecash to Member" icon="eye" :loading="loading">

        <ui-row form-group>
          <ui-col size="md-3">ECash balance:</ui-col>
          <ui-col size="md-9">
            
            <b style="font-size: 16px">
              Rp. {{balance.formatMoney()}}
            </b>
          </ui-col>
        </ui-row>
        <hr>

        <ui-row form-group>
          <ui-col size="xs-12">Select Member:</ui-col>
          <ui-col size="xs-9">
            <ui-textbox  v-model="form.memberId" ></ui-textbox>
          </ui-col>
          <ui-col size="xs-3">
            <ui-button icon="table" @click="searchMemberShow()" />
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-3">Enter the amount to be transferred:</ui-col>
          <ui-col size="md-9">
            <ui-textbox  v-model="form.amount" ></ui-textbox>
          </ui-col>
        </ui-row>
        
        
      </ui-panel>
    </ui-container>

    <ui-buttonbar>
      <ui-button bs-type="success" 
      @click="refreshData" 
      :loading="loading" 
      icon="refresh" />

      <ui-button @click="saveData" 
      :loading="loading" 
      icon="floppy-o" />
    </ui-buttonbar>

    <ui-modal @close="searchMemberDialog = false" 
    :display="searchMemberDialog" 
    title="Search Member" icon="search">
      <div slot="message">

        <ui-textbox v-model="keyword" placeholder="Type Member Username..." />
        <div style="height: 240px; overflow-y: scroll">
          <ui-list @tap="openMember"
          :data-source="rsMember_"
          data-field="usernameFullname" />
        </div>

      </div>
      <div slot="buttonbar">
        <ui-button @click="searchMemberDialog = false" bs-type="danger">Close</ui-button>
      </div>
    </ui-modal>

  </main-layout>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex';

  export default {

    data() {
      return {
        loading: false,
        searchMemberDialog: false,
        keyword: '',

        result: {
          state: '',
          message: '',
          data: [],
        },

        form: {
          amount: '',
          memberId: '',
        },

      };
    },

    computed: {
      ...mapGetters('TransferToMemberStore', {
        rowActive: 'rowActive',
        rowExtra: 'rowExtra',
      }),
      ...mapGetters('ActiveMembersStore', {
        rsMember: 'rs',
        rowMember: 'rowActive',
      }),

      balance() {
        if (parseInt(this.form.amount) > 0)
          return parseInt(this.rowExtra.cbalance) - parseInt(this.form.amount);
        else 
          return parseInt(this.rowExtra.cbalance);
      },

      rsMember_() {
        let rsMember = this.rsMember.map(member => {
          member.usernameFullname = member.username + ' / ' + member.first_name + ' ' + member.last_name;
          return member;
        });
        return this.rsMember.filter(member => {
          return member.username.toLowerCase().indexOf(this.keyword.toLowerCase()) > -1;
        });
      },
    },

    mounted() {
      this.refreshData();
    },

    methods: {
      
      ...mapActions('TransferToMemberStore', {
        fetchData: 'fetchData',
      }),

      ...mapActions('ActiveMembersStore', {
        fetchActiveMembers: 'fetchData',
        moveActiveMembers: 'moveData',
      }),

      searchMemberShow() {
        this.fetchActiveMembers();
        this.searchMemberDialog = true;
      },

      async refreshData() {
        this.loading = true;
        await this.fetchData();
        this.loading = false;
      },

      saveData() {
        this.loading = true;

        api.post('transferToMember/storeData', this.form).then(result => {

          if (result.state == 'success') {
            this.result = result;
            this.splash = true;

            this.$router.push('/saldo-transaksi-ecash/all-data');
          } else
            this.loading = false;
            this.result = result;
        });

      },

      openMember(value) {
        this.moveActiveMembers(value);
        this.searchMemberDialog = false;
        
        this.form.memberId = this.rowMember.username;
      },

    },

  }
</script>



