<template>
  <main-layout id="SalesOrderTransactionAllDataView">

    <ui-container fluid>
      <ui-panel title="Sales Order Transaction" :loading="loading" icon="list">

        <ui-row form-group>
          <ui-col size="md-12">

            <ui-list @tap="openData"
            :data-source="rs"
            data-field="bv" />

          </ui-col>
        </ui-row>

      </ui-panel>
    </ui-container>

    <ui-buttonbar>
      <ui-button bs-type="success" @click="refreshData" :loading="loading" icon="refresh"></ui-button>
    </ui-buttonbar>

  </main-layout>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex';

  export default {

    data() {
      return {
        loading: false,
        deleteDialog: false,
      };
    },

    computed: {

      ...mapGetters('SalesOrderTransactionStore', {
        rs: 'rs',
        rowActive: 'rowActive',
      }),

    },

    mounted() {
      if (this.rs.length <= 0)
        this.refreshData();
    },

    methods: {

      ...mapActions('SalesOrderTransactionStore', {
        fetchData: 'fetchData',
        moveData: 'moveData',
      }),

      refreshData() {
        this.loading = true;
        this.fetchData().then(result => {
          this.loading = false;
        });
      },

      openData(value) {
        this.$router.push('/sales-order-transaction/edit-data/' + value);
      },

    },

  }
</script>



