<template>
  <main-layout>
    <ui-splash :display="splash">{{ result.message }}</ui-splash>

    <ui-container fluid>

      <ui-alert :data-source="result.data"
      :display="result.state == 'invalid'"
      @close="result.state = ''">
        {{ result.message }}
      </ui-alert>

      <ui-panel title="Genealogy Jaringan" icon="users">

        <ui-row form-group>
          <ui-col size="md-1">Title</ui-col>
          <ui-col size="md-11">
            <ui-textbox v-model="form.title"></ui-textbox>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">Content</ui-col>
          <ui-col size="md-11">
            <ui-textarea v-model="form.content"></ui-textarea>
          </ui-col>
        </ui-row>

      </ui-panel>
    </ui-container>

    <ui-buttonbar>
      <ui-button @click="$router.go(-1)" 
      bs-type="danger"
      icon="arrow-left" />

      <ui-button @click="saveData" 
      :loading="loading" 
      icon="floppy-o" />
    </ui-buttonbar>

  </main-layout>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex';

  export default {

    /************************
    <id="_data">
    ************************/
    data() {
      return {
        loading: false,
        splash: false,

        result: {
          state: '',
          message: '',
          data: [],
        },

        form: {
          title: '',
          content: '',
          publicationId: '',
          postCategoryId: '',
          featuredImage: '',
        },
      };
    },

    /************************
    <id="_computed">
    ************************/
    computed: {
      /************************
      <id="_mapGetters">
      ************************/
      ...mapGetters('PostStore', {
        rowActive: 'rowActive',
      }),
    },

    /************************
    <id="_mounted">
    ************************/
    mounted() {
    },

    /************************
    <id="_methods">
    ************************/
    methods: {

      /************************
      <id="_mapActions">
      ************************/
      ...mapActions('PostStore', {
        createData: 'createData',
        moveLastData: 'moveLastData',
      }),

      /************************
      <id="_saveData">
      ************************/
      saveData() {
        this.loading = true;

        this.createData(this.form).then(result => {
          this.moveLastData();

          this.loading = false;
          this.result = result;

          this.splash = true;

          setTimeout(() => {
            this.splash = false;

            setTimeout(() => {
              this.$router.push('/posts/edit-data/' + this.rowActive.id);
            }, 500);
          }, 1500);

        }).catch(result => {

          this.loading = false;
          this.result = result;

        });
      },

    },

  }
</script>



