<template>
  <main-layout>
    
    <ui-container fluid>

      <ui-panel title="Network Register" icon="users">
        <h5>Please select the package member;</h5>
        <hr>
        <div v-for="(memberPackage, i) in packages" 
        :key="i"
        style="padding-bottom: 16px">
          <ui-row>
            <ui-col size="xs-6">
              <!-- :value="parseInt(memberPackage.price).formatMoney()"  -->
              <ui-box icon="cube" 
              title="Pay With Ecash" 
              @click="selectPackage('ecash', memberPackage.id)"
              :value="memberPackage.name" />
            </ui-col>

            <ui-col size="xs-6">
              <!-- :value="parseInt(memberPackage.price).formatMoney()"  -->
              <ui-box icon="cube" 
              title="Pay Transfer" 
              @click="selectPackage('transfer', memberPackage.id)"
              :value="memberPackage.name" />
            </ui-col>
          </ui-row>        
        </div>     
        <hr>

      </ui-panel>
    </ui-container>

    <ui-buttonbar>
      <ui-button bs-type="success" 
      @click="refreshData" 
      :loading="loading" 
      icon="refresh" />
    </ui-buttonbar>

  </main-layout>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex';

  export default {

    data() {
      return {
        loading: false,

        form: {
          upline: '',
          position: '',
          payment_method: '',
          package: '',
          order: {
            quantity: [],
            product_id: [],
            stock_product_id: [],
          },
        },
      };
    },

    mounted() {
      this.reloadParams();
      this.refreshData();
    },

    computed: {

      ...mapGetters('MemberPackageStore', {
        packages: 'rs',
      }),

    },

    methods: {

      ...mapActions('NetworkRegisterStore', {
        updateData: 'updateData',
      }),

      ...mapActions('MemberPackageStore', {
        fetchData: 'fetchData',
        movePackage: 'moveData',
      }),

      reloadParams() {
        this.form.position = this.$route.params.position;
        this.form.upline = this.$route.params.upline;
        // this.form.package = this.data.package;
        // this.form.payment_method = this.data.payment_method;
      },

      selectPackage(paymentMethod, packageId) {
        this.movePackage(packageId);
        this.updateData({
          upline: this.form.upline,
          position: this.form.position,
          payment_method: paymentMethod,
          package: packageId,
        });
        this.$router.push(
          '/network-register/' + 
          this.form.upline + '/' +
          this.form.position + '/' +
          paymentMethod + '/' + packageId
        );
      },
      
      refreshData() {
        this.loading = true;
        this.fetchData().then(result => {
          this.loading = false;
        });
      },

    },

  }
</script>



