export default {
  getMemberByPos(memberPos, rowActive) {
    let member = {
      up: {},
      left: {},
      right: {},
    };
    
    switch (memberPos) {
      case '':
        member.up = rowActive.model;
        member.up.pos = memberPos;
        
        member.left = rowActive.model2_l;
        member.left.pos = 'model2_l';
        
        member.right = rowActive.model2_r;
        member.right.pos = 'model2_r';
        
        break;
      
      case 'model2_l':
        member.up = rowActive.model2_l;
        member.up.pos = memberPos;
        
        member.left = rowActive.model3_2l_l;
        member.left.pos = 'model3_2l_l';
        
        member.right = rowActive.model3_2l_r;
        member.right.pos = 'model3_2l_r';
        
        break;
      
      case 'model2_r':
        member.up = rowActive.model2_r;
        member.up.pos = memberPos;
        
        member.left = rowActive.model3_2r_l;
        member.left.pos = 'model3_2r_l';
        
        member.right = rowActive.model3_2r_r;
        member.right.pos = 'model3_2r_r';
        
        break;

      case 'model3_2l_l':
        member.up = rowActive.model3_2l_l;
        member.up.pos = memberPos;
        
        member.left = rowActive.model4_32ll_l;
        member.left.pos = 'model4_32ll_l';
        
        member.right = rowActive.model4_32ll_r;
        member.right.pos = 'model4_32ll_r';
        
        break;

      // lr sama rl ketuker apa ngga?
      case 'model3_2l_r':
        member.up = rowActive.model3_2l_r;
        member.up.pos = memberPos;
        
        member.left = rowActive.model4_32lr_l;
        member.left.pos = 'model4_32lr_l';
        
        member.right = rowActive.model4_32lr_r;
        member.right.pos = 'model4_32lr_r';
        
        break;

      // lr sama rl ketuker apa ngga?
      case 'model3_2r_l':
        member.up = rowActive.model3_2r_l;
        member.up.pos = memberPos;
        
        member.left = rowActive.model4_32rl_l;
        member.left.pos = 'model4_32rl_l';
        
        member.right = rowActive.model4_32rl_r;
        member.right.pos = 'model4_32rl_r';
        
        break;

      case 'model3_2r_r':
        member.up = rowActive.model3_2r_r;
        member.up.pos = memberPos;
        
        member.left = rowActive.model4_32rr_l;
        member.left.pos = 'model4_32rr_l';
        
        member.right = rowActive.model4_32rr_r;
        member.right.pos = 'model4_32rr_r';
        
        break;
      
    }
    
    return member;
  }
}


