<template>
  <main-layout id="RiwayatUvEditDataView">
    <ui-splash :display="splash">{{ result.message }}</ui-splash>

    <ui-container fluid>

      <ui-alert :data-source="result.data"
      :display="result.state == 'invalid'"
      @close="result.state = ''">
        {{ result.message }}
      </ui-alert>

      <ui-panel title="Riwayat UV" icon="eye">

        <ui-row form-group>
          <ui-col size="md-2">Personal UV</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.personal_bv"></ui-label>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-2">Bulan</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.month"></ui-label>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-2">Tahun</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.year"></ui-label>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-2">UV Kiri</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.left_bv"></ui-label>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-2">UV Kanan</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.right_bv"></ui-label>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-2">Layak</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.is_qualified"></ui-label>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-2">Level</ui-col>
          <ui-col size="md-10">
            <ui-label v-model="form.level"></ui-label>
          </ui-col>
        </ui-row>


      </ui-panel>
    </ui-container>

    <ui-buttonbar>
      <ui-button @click="$router.go(-1)" 
        bs-type="success"
        icon="arrow-left" />
    </ui-buttonbar>


  </main-layout>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex';

  export default {

    data() {
      return {
        loading: false,
        splash: false,
        deleteDialog: false,

        result: {
          state: '',
          message: '',
          data: [],
        },

        form: {
          id: '',
          personal_bv: '',
          left_bv: '',
          right_bv: '',
          is_qualified: '',
          month: '',
          year: '',
          level: '',
        },
      };
    },

    computed: {
      ...mapGetters('RiwayatUvStore', {
        rowActive: 'rowActive',
      }),
    },

    mounted() {
      this.moveData(this.$route.params.id);

      this.form.id = this.rowActive.id;
      this.form.personal_bv = this.rowActive.personal_bv;
      this.form.left_bv = this.rowActive.left_bv;
      this.form.right_bv = this.rowActive.right_bv;
      this.form.is_qualified = this.rowActive.is_qualified;
      this.form.month = this.rowActive.month;
      this.form.year = this.rowActive.year;
      this.form.level = this.rowActive.level;

    },

    methods: {

      ...mapActions('RiwayatUvStore', {
        moveData: 'moveData',
      }),


    },

  }
</script>



