export default {
  namespaced: true,

  /************************
  function _state()
  ************************/
  state: {
    rs: [],
    rsMemberPv: {},
    rsKlaim: [],
    rowActive: {},
    rowActiveKlaim: {},
  },

  /************************
  function _mutations()
  ************************/
  mutations: {

    setDataSource(state, dataSource) {
      state.rsKlaim = dataSource.rewards;
      state.rs = dataSource.model;
    },

    setDataPv(state, dataSource) {
      state.rsMemberPv = dataSource.memberpv;
    },

    addData(state, data) {
      state.rs.push(data);
    },

    setData(state, data) {
      state.rs.splice(state.rowActive.index, 1, {
        ...state.rs[state.rowActive.index],
        ...data,
      });
    },

    removeData(state) {
      state.rs.splice(state.rowActive.index, 1);
      state.rowActive = {};
    },

    setRowActiveByIndex(state, index) {
      let rowActive = state.rs[index];
      rowActive.index = index;

      state.rowActive = rowActive;

      let rowActiveKlaim = state.rsKlaim[index];
      rowActiveKlaim.index = index;

      state.rowActiveKlaim = rowActiveKlaim;
    },

    setRowActiveById(state, id) {

      let rowActive = {};
      let i = -1;

      state.rs.forEach(row => {
        i++;

        if (row.id == id) {
          rowActive = row;
          rowActive.index = i;
        }

      });

      state.rowActive = rowActive;
    },

    setRowActiveKlaimById(state, id) {

      let rowActiveKlaim = {};
      let i = -1;

      state.rsKlaim.forEach(row => {
        i++;

        if (row.id == id) {
          rowActiveKlaim = row;
          rowActiveKlaim.index = i;
        }

      });

      state.rowActiveKlaim = rowActiveKlaim;
    },

  },

  /************************
  function _actions()
  ************************/
  actions: {

    fetchData({ commit }) {
      return new Promise((resolve, reject) => {
        api.post('bonusReward/readAllData', {
          computed: true
        }).then(result => {

          if (result.state == 'success') {
            commit('setDataSource', result.data);
            resolve(result);
          } else
            reject(result);
        });
      });
    },

    storeData({ commit }, data) {
      return new Promise((resolve, reject) => {
        api.post('bonusReward/storeData', data).then(result => {

          if (result.state == 'success') {
            // commit('setDataSource', result.data);
            resolve(result);
          } else {
            reject(result);
          }

        });
      });
    },

    moveFirstData({ commit }) {
      commit('setRowActiveByIndex', 0);
    },

    moveLastData({ state, commit }) {
      commit('setRowActiveByIndex', state.rs.length - 1);
    },

    moveData({ commit }, id) {
        commit('setRowActiveById', id);
      
    },

    readOneData({ commit }, data) {
      return new Promise((resolve, reject) => {
      api.post('bonusReward/readOneData', data).then(result => {

        if (result.state == 'success') {
          commit('setDataPv', result.data);
          resolve(result);
        } else {
          reject(result);
        }

      });
    });
    
  },

    
    moveDataKlaim({ commit }, id) {
      commit('setRowActiveKlaimById', id);
    },


  },

  /************************
  function _getters()
  ************************/
  getters: {

    rs(state) {
      // return state.rs;
      return state.rs.map(row => {
        row.bv = `Nama : ${row.name} / Bonus. : ${row.bonus}`;
        return row;
      });
    },

    rowActive(state) {
      return state.rowActive;
    },

    dataPv(state) {
      return state.rsMemberPv;
    },

    rsKlaim(state) {
      return state.rsKlaim.map(row => {
        row.bv = `Nama : ${row.name} / Bonus. : ${row.bonus}`;
        return row;
      });
    },

    rowActiveKlaim(state) {
      return state.rowActiveKlaim;
    },


  },

}



