<template>
  <div id="AlertComponent" class="ui-alert">
    <transition enter-active-class="animated slideInDown"
    leave-active-class="animated fadeOut">
      <div v-if="display" class="ui-alert alert" :class="'alert-' + type">
        <button v-if="closeButton" type="button" @click="handleClose" class="close">
          <i class="fa fa-close"></i>
        </button>
        <h4>
          <i v-if="icon" :class="'fa fa-' + icon"></i>
          <slot></slot>
        </h4>
        <p v-if="dataSource">
          <ul>
            <!-- <li v-for="row in dataSource">{{ row[dataField] }}</li> -->
            <li v-for="(row, i) in dataSource" :key="i">{{ row }}</li>
          </ul>
        </p>
      </div>
    </transition>
  </div>
</template>

<script>
  export default {

    /************************
    <id="_props">
    ************************/
    props: {

      type: {
        type: String,
        default: 'danger',
      },

      display: {
        type: Boolean,
        default: false,
      },

      closeButton: {
        type: Boolean,
        default: true,
      },

      icon: {
        type: String,
        default: '',
      },

      dataSource: {
        type: Array,
        default: () => [],
      },

      dataField: {
        type: String,
        default: 'id',
      },

    },

    /************************
    <id="_methods">
    ************************/
    methods: {

      /************************
      <id="_handleClose">
      ************************/
      handleClose(event) {
        this.$emit('close');
      },

    },
  }
</script>



