<template>
  <main-layout>
    <ui-splash :display="splash">{{ result.message }}</ui-splash>

    <ui-container fluid>

      <ui-alert :data-source="result.data"
      :display="result.state == 'invalid'"
      @close="result.state = ''">
        {{ result.message }}
      </ui-alert>

      <ui-panel title="Change Password" icon="edit" :loading="loading">

        <ui-row form-group>
          <ui-col size="md-1">Old Password *</ui-col>
          <ui-col size="md-11">
            <ui-textbox type="password" v-model="form.oldPassword" ></ui-textbox>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">New Password *</ui-col>
          <ui-col size="md-11">
            <ui-textbox type="password" v-model="form.newPassword" ></ui-textbox>
          </ui-col>
        </ui-row>

        <ui-row form-group>
          <ui-col size="md-1">Confirm Password *</ui-col>
          <ui-col size="md-11">
            <ui-textbox type="password" v-model="form.confirmPassword" ></ui-textbox>
          </ui-col>
        </ui-row>


      </ui-panel>
    </ui-container>

    <ui-buttonbar>
      <ui-button @click="saveData" 
      :loading="loading" 
      icon="floppy-o" />
    </ui-buttonbar>

  </main-layout>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex';

  export default {

    data() {
      return {
        loading: false,
        splash: false,
    
        result: {
          state: '',
          message: '',
          data: [],
        },

        form: {
          oldPassword: '',
          newPassword: '',
          confirmPassword: '',
        },
      };
    },

    methods: {

      saveData() {
        this.loading = true;

        api.post('changePassword/storeData', this.form).then(result => {

          if (result.state == 'success') {
            this.result = result;
            this.splash = true;

            setTimeout(() => {
              this.splash = false;
              this.loading = false;
              this.form.oldPassword = '';
              this.form.newPassword = '';
              this.form.confirmPassword = '';

            }, 2000);
          } else
            this.loading = false;
            this.result = result;
        });

      },

    },

  }
</script>



