export default {
  namespaced: true,

  state: {
    products: [],
    data: {
      package: '',
      payment_method: '',
      order: [],
    },
  },

  mutations: {

    setProductsSource(state, dataSource) {
      state.products = dataSource;
    },

    setDataSource(state, dataSource) {
      state.data = dataSource;
    },

  },

  actions: {

    fetchData({ commit }, data) {
      return new Promise((resolve, reject) => {
        api.post('upgradeMembership/readAllData', data).then(result => {

          if (result.state == 'success') {
            commit('setProductsSource', result.data);
            resolve(result);
          } else
            reject(result);
        });
      });
    },

    updateData({ commit }, data) {
      commit('setDataSource', data);
    },

    createData({ commit }, data) {
      return new Promise((resolve, reject) => {
        api.post('upgradeMembership/createData', data).then(result => {
          if (result.state == 'success')
            resolve(result);
          else
            reject(result);
        });

        commit('setDataSource', data);
      });
    },

  },

  getters: {

    products(state) {
      return state.products;
    },

    data(state) {
      return state.data;
    },

  },

}



