<style scoped>
  .ui-col {
    padding: 16px;
    padding-bottom: 8px;
  }
</style>

<template>
  <main-layout id="HomeView">

    <ui-container fluid>
      <ui-panel title="Dashboard" icon="dashboard">

        <ui-row>
          <ui-col size="xs-6">
            <ui-box title="Cashback Bonus" 
            icon="money" 
            :value="parseInt(form.bcashback).formatMoney()" />
          </ui-col>

          <ui-col size="xs-6">
            <ui-box title="Supermatch Bonus" 
            icon="money" 
            :value="parseInt(form.bmatching).formatMoney()" />
          </ui-col>

          <ui-col size="xs-6">
            <ui-box title="Qualified Bonus" 
            icon="money" 
            :value="parseInt(form.bqualified).formatMoney()" />
          </ui-col>

          <ui-col size="xs-6">
            <ui-box title="Pairing Bonus" 
            icon="money" 
            :value="parseInt(form.bpairing).formatMoney()" />
          </ui-col>

          <ui-col size="xs-12">
            <ui-box title="Sponsor Bonus" 
            icon="money" 
            :value="parseInt(form.bsponsor).formatMoney()" />
          </ui-col>
        </ui-row>

      </ui-panel>
    </ui-container>

    <ui-buttonbar>
      <ui-button bs-type="primary" 
      @click="refreshData" 
      :loading="loading" 
      icon="refresh" />
    </ui-buttonbar>

  </main-layout>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex';

  export default {

    data() {
      return {
        loading: false,
      };
    },

    computed: {
      ...mapGetters('DashboardStore', {
        form: 'rowActive',
      }),
    },

    mounted() {
      this.refreshData();
    },

    methods: {
      
      ...mapActions('DashboardStore', {
        fetchData: 'fetchData',
      }),

      async refreshData() {
        this.loading = true;
        await this.fetchData();
        this.loading = false;
      },

    },

  }
</script>



