<template>
  <main-layout>
    <ui-splash :display="splash">{{ result.message }}</ui-splash>

    <ui-container fluid>

      <ui-alert :data-source="result.data"
      :display="result.state == 'invalid'"
      @close="result.state = ''">
        {{ result.message }}
      </ui-alert>
      
      <ui-panel title="Network Register" icon="users">
        
        <ui-box icon="cube" 
        :title="(this.form.payment_method == 'ecash')?'Pay With Ecash':'Pay Transfer'" 
        :value="memberPackage.name" />
        <hr>
        
        <ui-row v-for="(row, i) in products" :key="i" form-group>
          <ui-col size="md-12">
            <b>{{ row.name }}</b><br>
            <i>Price: {{ parseInt((form.package == 1)?row.specialcprice:row.specialpprice).formatMoney() }}</i>
          </ui-col>
          <ui-col size="md-12">
            <ui-textbox v-model="row.qty" placeholder="Qty" />
          </ui-col>
          <ui-col size="md-12">
            <i>
              Total: 
              {{ parseInt(((form.package == 1)?row.specialcprice:row.specialpprice) * row.qty).formatMoney() }}
            </i>
          </ui-col>
        </ui-row>

      </ui-panel>
    </ui-container>

    <ui-buttonbar>
      <ui-button @click="$router.go(-1)" 
      bs-type="danger"
      icon="arrow-left" />
      
      <ui-button @click="saveData" 
      :loading="loading" 
      icon="check" />
    </ui-buttonbar>

  </main-layout>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex';

  export default {

    data() {
      return {
        loading: false,
        splash: false,

        result: {
          state: '',
          message: '',
          data: [],
        },

        form: {
          upline: '',
          position: '',
          payment_method: '',
          package: '',
          order: {
            quantity: [],
            product_id: [],
            stock_product_id: [],
          },
        },
      };
    },

    computed: {

      ...mapGetters('NetworkRegisterStore', {
        products: 'products',
      }),

      ...mapGetters('MemberPackageStore', {
        memberPackage: 'rowActive',
      }),

      total() {
        let total = 0;

        this.products.forEach((row) => {
          if (row.qty)
            total += parseInt(row.specialprice * row.qty);
        });

        return total;
      },

    },

    mounted() {
      this.reloadParams();
      this.refreshData();
    },

    methods: {
     
      ...mapActions('NetworkRegisterStore', {
        fetchData: 'fetchData',
        updateData: 'updateData',
        createData: 'createData',
      }),
      
      reloadParams() {
        this.form.upline = this.$route.params.upline;
        this.form.position = this.$route.params.position;
        this.form.payment_method = this.$route.params.paymentMethod;
        this.form.package = this.$route.params.package;        
      },

      refreshData() {
        let data = {
          package: this.$route.params.package,
        };

        this.loading = true;
        this.fetchData(data).then(result => {
          this.loading = false;
        });
      },

      saveData() {
        if (this.total <= 0) return;

        this.products.forEach((row, i) => {
          this.form.order.product_id[i] = row.id;
          this.form.order.stock_product_id[i] = row.id;
          this.form.order.quantity[i] = (row.qty?parseInt(row.qty):0); 
        });

        this.updateData(this.form);

        this.$router.push('/network-register/' + 
        this.form.upline + '/' + 
        this.form.position + '/' + 
        this.form.payment_method + '/' + 
        this.memberPackage.id + 
        '/register');
      },

    },

  }
</script>



